import React, { Component, PropTypes } from 'react';
import KlassName from 'teapan/klassName';

class Abort extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		const props = this.props;
		const {
			code,
			className,
			children,
			...others
		} = props;

		const klassName = KlassName(
			'abort',
			className
		);

		return (
			<div {...others} className={klassName}>
				{ children ?
					<div className="abort-content">
						<h3>{code}</h3>
						{children}
					</div> :
					<h2 className="abort-title">{code}</h2>
				}
			</div>
		);
	}
};

Abort.propTypes = {
	code: PropTypes.string,
	className: PropTypes.string,
	children: PropTypes.node,
};

Abort.defaultProps = {
};

export default Abort;